html {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  font-size: 16px; }
  @media (max-width: 640px) {
    html {
      font-size: 14px; } }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #171F3F; }

.footer {
  background: #171F3F;
  color: #ffffff;
  padding-bottom: 132px;
  height: auto; }
  .footer__email {
    color: #0593fb;
    font-weight: 500;
    text-decoration: none;
    margin-left: 5vw;
    font-size: 1.25rem;
    line-height: 2rem;
    position: relative; }
    .footer__email::after {
      position: absolute;
      margin: 30px 0 -20px -100%;
      content: "";
      border-bottom: 3px solid #0593fb;
      border-radius: 41px;
      width: 100%; }
  .footer__link {
    margin-right: 10px;
    margin-bottom: 32px; }
  .footer__links-wrapper {
    margin-left: 5vw;
    display: flex;
    flex-direction: row; }
  .footer__paragraph {
    max-width: 90vw;
    margin-left: 5vw;
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 2.25rem; }
  .footer__title {
    margin-left: 5vw;
    font-weight: 600;
    font-size: 2rem;
    line-height: 44px;
    margin-top: 140px; }
    .footer__title:before {
      position: absolute;
      margin-top: -20px;
      content: "";
      height: 4px;
      width: 80px;
      border-radius: 8px;
      background: #0593fb;
      transform: matrix(1, 0, 0, -1, 0, 0); }

.main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: 16px solid #171f3f; }
  @media (max-width: 640px) {
    .main {
      border: none; } }

.header {
  padding: 180px 0px 0 100px;
  max-width: 761px;
  width: 100%; }
  @media (max-width: 640px) {
    .header {
      padding: 64px 16px 0 16px; } }
  .header__image {
    display: block;
    width: 104px;
    height: 104px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1px; }
  .header__image-wrapper {
    display: block;
    margin: 0;
    padding: 0;
    text-align: center;
    width: 122px;
    height: 122px;
    border: 8px solid #cbd2d9;
    border-radius: 50%; }
  .header__email {
    color: #0593FB;
    font-weight: 500;
    text-decoration: none;
    position: relative; }
    .header__email::after {
      position: absolute;
      margin: 30px 0 -20px -100%;
      content: "";
      border-bottom: 3px solid #0593FB;
      border-radius: 41px;
      width: 100%; }
  .header__link {
    color: #0593FB;
    font-weight: 500; }
  .header__paragraph {
    max-width: 681px;
    font-weight: 300;
    font-size: 20px;
    line-height: 36px;
    color: #52606d; }
  .header__title {
    font-weight: 600;
    font-size: 3, 33rem;
    line-height: 56px; }
    @media (max-width: 640px) {
      .header__title {
        font-size: 1.85rem;
        line-height: 2.57rem; } }

.project {
  width: 100%;
  background: #ffffff;
  box-shadow: 0px 6px 16px rgba(24, 41, 67, 0.09);
  border-radius: 8px;
  position: relative;
  padding-bottom: 64px; }
  @media (max-width: 640px) {
    .project {
      width: 90%;
      margin-bottom: 40px; } }
  .project__content {
    margin-left: 32px;
    margin-right: 32px;
    margin-top: 48px; }
    @media (max-width: 640px) {
      .project__content {
        margin-left: 2rem;
        margin-right: 2rem; } }
  .project__description {
    font-weight: 300;
    font-size: 1.125rem;
    line-height: 2rem;
    color: #52606d; }
  .project__link {
    padding: 10px;
    color: #0593FB;
    text-decoration: none;
    cursor: pointer; }
    .project__link:hover {
      text-decoration: underline; }
  .project__link-icon {
    margin-right: 6px; }
  .project__link-separator {
    background: #cbd2d9;
    color: #cbd2d9;
    width: 2px;
    height: 24px;
    margin: 0 20px 0 10px;
    transform: matrix(-1, 0, 0, 1, 0, 0); }
  .project__links-wrapper {
    display: flex;
    position: absolute;
    bottom: 0;
    width: 100%;
    background: #f5f7fa;
    height: 64px;
    align-items: center;
    justify-content: flex-end; }
  .project__logo {
    margin-bottom: -10px; }
  .project__title {
    font-weight: 600;
    font-size: 1.25rem;
    word-wrap: break-word;
    line-height: 2rem;
    color: #171f3f; }

.project-list-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 24px;
  grid-row-gap: 24px;
  margin-left: 100px;
  margin-right: 100px; }
  @media (max-width: 640px) {
    .project-list-wrapper {
      display: flex;
      flex-direction: column;
      row-gap: 20px;
      margin: 0;
      align-items: center; } }

.projects {
  flex-direction: column;
  background: #F5F7FA;
  padding-bottom: 128px; }

.projects-header {
  margin-left: 100px;
  display: block;
  max-width: 580px;
  width: 100%; }
  @media (max-width: 640px) {
    .projects-header {
      text-align: center;
      width: 90%;
      margin: 0 auto; } }
  .projects-header__title {
    font-weight: 600;
    font-size: 36px;
    line-height: 48px;
    color: #171f3f; }
  .projects-header__paragraph {
    font-weight: 300;
    font-size: 20px;
    line-height: 36px;
    color: #52606d; }

.skill-group {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 100px;
  width: 100%; }
  @media (max-width: 640px) {
    .skill-group {
      grid-template-columns: 1fr;
      margin-bottom: 48px; } }
  .skill-group__title {
    margin: 0;
    font-weight: 600;
    font-size: 2rem;
    line-height: 68px;
    width: 100%;
    position: relative; }
    .skill-group__title:before {
      position: absolute;
      content: "";
      height: 4px;
      width: 80px;
      border-radius: 8px;
      transform: matrix(1, 0, 0, -1, 0, 0); }
    .skill-group__title--first::before {
      background: #06c847; }
    .skill-group__title--second::before {
      background: #653cad; }
    .skill-group__title--third::before {
      background: #2251cc; }

.skill-list {
  margin-left: 100px;
  margin-top: 24px;
  column-count: 2;
  column-gap: 30px;
  font-size: 1rem; }
  @media (max-width: 640px) {
    .skill-list {
      column-count: 1;
      margin-left: 0px;
      font-size: 1rem;
      padding-inline-start: 16px; } }
  .skill-list__element {
    font-weight: normal;
    line-height: 40px;
    max-width: 279px;
    width: 100%;
    list-style-type: none;
    color: #52606d; }
    .skill-list__element--first {
      list-style-image: url(../assets/img/check-circle.png); }
    .skill-list__element--second {
      list-style-image: url(../assets/img/check-circle-purple.png); }
    .skill-list__element--third {
      list-style-image: url(../assets/img/check-circle-blue.png); }

.skills {
  margin-top: 200px;
  margin-left: 100px; }
  @media (max-width: 640px) {
    .skills {
      margin: 20px 20px 0 20px; } }
  .skills__title {
    width: 100%;
    font-weight: 600;
    font-size: 36px;
    line-height: 48px; }
    @media (max-width: 640px) {
      .skills__title {
        text-align: center; } }
  .skills__separator {
    width: 100%;
    margin-top: -15px;
    border-bottom: 2px solid #cbd2d9;
    opacity: 0.5;
    margin-bottom: 156px; }
    @media (max-width: 640px) {
      .skills__separator {
        text-align: center;
        margin-bottom: 62px; } }
